import React, { useState } from "react";
import { useTranslation } from "react-i18next";

const AllFAQs = () => {
  const { t } = useTranslation();

  // الأسئلة
  const faqs = [
    { question: t("faq.question1"), answer: t("faq.answer1") },
    { question: t("faq.question2"), answer: t("faq.answer2") },
    { question: t("faq.question3"), answer: t("faq.answer3") },
    { question: t("faq.question4"), answer: t("faq.answer4") },
    { question: t("faq.question5"), answer: t("faq.answer5") },
    { question: t("faq.question6"), answer: t("faq.answer6") },
    { question: t("faq.question7"), answer: t("faq.answer7") },
    { question: t("faq.question8"), answer: t("faq.answer8") },
  ];

  // حالة لمعرفة ما إذا كان يجب إظهار المزيد من الأسئلة
  const [showAll, setShowAll] = useState(false);

  // دالة لتبديل عرض الأسئلة
  const toggleShowAll = () => {
    setShowAll(!showAll);
  };

  return (
    <div className="container py-2">
      <h2 className="text-center mb-4">{t("faq.title")}</h2>
      <div className="accordion" id="faqAccordion">
        {/* عرض الأسئلة الأولى فقط أو جميعها بناءً على الحالة */}
        {faqs.slice(0, showAll ? faqs.length : 4).map((faq, index) => (
          <div key={index}>
            <div className="p-2" id={`heading${index}`}>
              <h5 className="mb-0">
                <button
                  className="btn btn-link"
                  type="button"
                  data-bs-toggle="collapse"
                  data-bs-target={`#faq${index}`}
                  aria-expanded="false"
                  aria-controls={`faq${index}`}
                >
                  {faq.question}
                </button>
              </h5>
            </div>
            <div
              id={`faq${index}`}
              className="collapse"
              aria-labelledby={`heading${index}`}
              data-bs-parent="#faqAccordion"
            >
              <div className="card-body">
                <p>{faq.answer}</p>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* زر "المزيد" يظهر فقط إذا كان هناك أكثر من 4 أسئلة */}
      {faqs.length > 4 && (
        <div className="text-center mt-4">
          <button className="btn btn-primary" onClick={toggleShowAll}>
            {showAll ? t("faq.showLess") : t("faq.showMore")}
          </button>
        </div>
      )}
    </div>
  );
};

export default AllFAQs;
